body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-right: 1em;
}
.Category {
    display: inline-grid;
    width: 30%;
    /* max-height: 300px;
    overflow: scroll; */
    background-color: var(--accent-light-blue);
    border-radius: 8px;
    margin: 1em;
}

.Category-title-box {
    padding: 0.5em 1em 0.5em 1.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.Category h1 {
    font-size: 1.25em;
}

.Category-items {
    /* overflow: scroll; */
}

.Category ul li {
    /* margin: 2.5em */
}
.AddEditForm {
    position: absolute;
    width: 100vw;
    height: 100vh;
    background: rgb(44,44,44,0.5);
    z-index: 100;
    top: 0;
    left: 0;
}

.AddEditForm-box {
    position: relative;
    top: 20vh;
    margin: auto;
    width: 600px;
    height: 300px;
    background-color: var(--accent-light-blue);
    border-radius: 8px;
}

.AddEditForm-form {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.AddEditForm-form form {
    text-align: center;
}

.AddEditForm-form input {
    display: block;
}

.AddEditForm-exit-btn {
    position: absolute;
    right: 0;
    margin: 1.5em;
}
.MasterList {
    /* display: flex;
    justify-content: center; */
}
.MyListItem-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.MyListEdit {
    max-width: 600px;
    margin: 1em auto;
    padding: 1em;
    background-color: var(--accent-light-blue);
    border-radius: 7px;
}

.MyListEdit-title {
    text-align: center;
}
:root {
  --accent-light-blue:#F0F3F8;
}

*{
  font-family: 'Raleway', sans-serif;
  font-size: 1em;
}

.hidden {
  display: none;
}

h1 {
  font-size: 1.5em;
}

button {
  margin: 0.5em;
  padding: 0.5em 0.75em;
  background-color: inherit;
  color: #1455DD;
  border: 1px solid #1455DD;
  border-radius: 3px;
  outline: none;
}

input {
  border: none;
  background: inherit;
  border-bottom: 1px solid black;
  padding: 0.5em;
  width: 300px;
  margin: 1em;
  outline: none;
}

ul {
  padding-left: 1.5em;
}

li {
  list-style: none;
}

.App {
  max-width: 1080px;
  margin: 0 auto;
}

.App-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0.5em;
}
